import { observable } from 'mobservable';
import { DisplayMode } from '@microsoft/sp-client-base';

const DEFAULT_TITLE: string = 'SharePoint Web Part Workbench';

export interface IWorkBenchStore {
  appTitle: string;
  isEditing: boolean;
  displayMode: DisplayMode;
  isDetailsPaneVisible: boolean;
  /* tslint:disable:no-any */
  addinType ?: new () => any;
  /* tslint:enable:no-any */
  toggleEdit: () => void;
}

export class WorkBenchStore {
  public appTitle: string;
  @observable public isEditing: boolean;
  public isDetailsPaneVisible: boolean;

  constructor() {
    this.appTitle = DEFAULT_TITLE;
    this.isEditing = true;
    this.isDetailsPaneVisible = false;
  }

  public get displayMode(): DisplayMode {
    return this.isEditing ? DisplayMode.Edit : DisplayMode.Read;
  }

  public toggleEdit(): void {
    this.isEditing = !this.isEditing;
  }

  public toggleDetailsPane(): void {
    this.isDetailsPaneVisible = !this.isDetailsPaneVisible;
  }
}
